<script>
import axios from "axios";
export default {
  data() {
    return {
      rating: 4,
      courses: {},
      
      slider: {},
      viewdialog: false,
      timeout: 5000,
      msg: "",
      appLoading: false,
      snackbar: false,
      slideInterval: 7000,
      services: [],
      slideNo: 0,
      list: {},
      card: [
        {
          img: require("../assets/Images/icon-2.png"),
          heading: "Training programs",
          text: "At GTSL we offer training programs to individuals or organizations interested in learning about logistics",
        },

        {
          img: require("../assets/Images/icon.png"),
          heading: "Research and Publications",
          text: "We conduct research on logistics topics and publish articles, reports etc. to promote best practices in logistics.",
        },
        {
          img: require("../assets/Images/icon-1.png"),
          heading: "Certified courses",
          text: "At GTSL we may offer certification courses for individuals seeking to become certified logistics professionals.",
        },
      ],
    };
  },

  mounted() {
    this.SliderContent();
    this.getServices();
    this.hompageContent();
    this.userView();
  },

  methods: {
    userView() {
      this.appLoading = true;
      axios({
        method: "GET",
        headers: {
          siteId: "6436362a65dc48eef99acd34",
        },
        url: "/user/course/list",
      })
        .then((response) => {
          this.courses = response.data.data[1];
          this.appLoading = false;
        })
        .catch((err) => {
          this.ServerError = true;
          console.log(err);
        });
    },
    getServices() {
      this.appLoading = true;
      axios({
        method: "GET",
        headers: {
          siteId: "6436362a65dc48eef99acd34",
        },
        url: "/user/services/list",
      })
        .then((response) => {
          //  this.$router.go();
          this.services = response.data.data;
          this.varx = response.data.data[0].link;
          this.appLoading = false;
        })
        .catch((err) => {
          this.ServerError = true;
          console.log(err);
        });
    },

    SliderContent() {
      this.appLoading = true;
      axios({
        method: "GET",
        headers: {
          siteId: "6436362a65dc48eef99acd34",
        },
        url: "/user/slider/details",
      })
        .then((response) => {
          //  this.$router.go();
          this.slider = response.data.data;
          this.appLoading = false;
        })
        .catch((err) => {
          this.ServerError = true;
          console.log(err);
        });
    },
    hompageContent() {
      this.appLoading = true;
      axios({
        method: "GET",
        headers: {
          siteId: "6436362a65dc48eef99acd34",
        },
        url: "/user/home/contents",
      })
        .then((response) => {
          //  this.$router.go();
          this.list = response.data.data;
          this.appLoading = false;
        })
        .catch((err) => {
          this.ServerError = true;
          console.log(err);
        });
    },
  },
};
</script>


